.form-container{
    margin: 4rem 6rem;
    color: #2a2a2a;
}

.form-container h1 {
    color: teal;
}

.form-container form {
    padding-top: 3rem;
    display: flex;
    flex-direction: column;
    width: 50%;
    margin: auto;
}

.form-container input {
    height: 3rem;
    padding: 0 1rem;
    margin-bottom: 2rem;
    border-radius: .3rem;
    border: 1px solid #2a2a2a;
}

.form-container textarea {
    padding: 1rem;
    margin-bottom: 2rem;
    border-radius: 0.3rem;
    border: 1px solid #2a2a2a;
}

.form-container button {
    display: inline;
    height: 2.5rem;
    font-size: 1rem;
}
.form-container button:hover {
    color: white;
    background-color: rgb(244, 121, 26);
}


@media screen and (max-width: 850px){
    .form-container{
        margin: 4rem 2rem;
    }
    .form-container form{
        padding-top: 2rem;
        width: 90%;
    }
}